import { Form } from "antd";
import { useState } from "react";
import { Fn } from "../../helper/function";
import { AuthService } from "../../services/authService";
import { MyFormItem } from "../core/Input";
import { MyBox } from "../core/MyBox";
import { MyButton } from "../core/MyButton";

export default function ChangePasswordComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [oldPass, setOldPass] = useState('');
    const [newPass, setNewPass] = useState('');
    const [reNewPass, setReNewPass] = useState('');
    const [msgErr, setMsgErr] = useState('');

    function handleSubmitForm() {
        setIsLoading(true);
        var body = {
            oldPass: oldPass,
            newPass: newPass
        };

        AuthService.UpdatePassword(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
        }, undefined, () => {
            setIsLoading(false);
        });
    }

    function handleChangeNewPass(value: string) {
        validateNewPasswordInput(value, reNewPass);
    }

    function handleChangeReNewPass(value: string) {
        validateNewPasswordInput(newPass, value);
    }

    function validateNewPasswordInput(_newPass: string, _reNewPass: string) {
        if (_newPass !== _reNewPass) {
            setMsgErr('Nhập lại mật khẩu mới không hợp lệ');
        } else {
            setMsgErr('');
        }
    }

    return (
        <MyBox
            isLoading={isLoading}
            title="Thay đổi mật khẩu">
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Password
                    label="Mật khẩu cũ"
                    value={oldPass}
                    setValue={setOldPass} />

                <MyFormItem.Password
                    label="Mật khẩu mới"
                    onChange={handleChangeNewPass}
                    value={newPass}
                    setValue={setNewPass} />

                <MyFormItem.Password
                    label="Nhập lại mật khẩu mới"
                    onChange={handleChangeReNewPass}
                    value={reNewPass}
                    setValue={setReNewPass} />

                <p>{msgErr}</p>
                <MyButton
                    disabled={msgErr !== '' || oldPass === '' || newPass === '' || reNewPass === ''}
                    isLoading={isLoading}
                    value="Thay đổi"
                    type="success"
                    htmlType="submit" />
            </Form>
        </MyBox>
    );
}